import MasterPage from '../componentes/MasterPage'
import fetch from 'isomorphic-unfetch'
import Precio from '../componentes/Precio'
const Index = (props) => (
        <MasterPage>
            <div className='row'>
                <div className='col-12'>
                    <Precio
                        precio = {props.precioBitcoin}
                        precio7Dias = {props.precio7Dias}
                    />
                </div>
                {console.log(props.precio7Dias)}
                <div className='col-md-8'>
                    <h2>Precio del Bitcoin</h2>
                </div>
                <div className='col-md-4'>
                    <h2>Precio del Bitcoin</h2>
                </div>
            </div>
        </MasterPage>
    )

    Index.getInitialProps= async ()=> {
        const consulta = await fetch('https://api.binance.com/api/v3/ticker/24hr?symbol=BTCUSDT')
        const consulta7Dias = await fetch('https://api.binance.com/api/v3/klines?symbol=BTCUSDT&interval=1d&limit=7')
       
        const resultado7Dias =await consulta7Dias.json()
        const resultado =await consulta.json()
        return {
            precioBitcoin : resultado,
            precio7Dias : resultado7Dias
        }
    }

export default Index;